import Vue from 'vue'
import Router from 'vue-router'
import Home from '../components/Home.vue'
import HomeMobile from '../components/HomeMobile.vue'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/mobile',
      name: "HomeMobile",
      component: HomeMobile
    }
    
  ]
})



