<script>
export default {
    data() {
        return {
            anchor_isActive: [],
            section_position: [],
        }
    },
    created() {
        window.addEventListener('mousewheel', this.getSectionY, {
            passive: false
        });
    },
    mounted:function() {
        for (let index = 0; index < 5; index++) {
            this.anchor_isActive[index] = false;
        }
        this.section_position[0] = this.$refs.first_section_ref.getBoundingClientRect().top;
        this.section_position[1] = this.$refs.second_section_ref.getBoundingClientRect().top;
        this.section_position[2] = this.$refs.third_section_ref.getBoundingClientRect().top;
        this.section_position[3] = this.$refs.fourth_section_ref.getBoundingClientRect().top;
        this.section_position[4] = this.$refs.last_section_ref.getBoundingClientRect().top;
        console.log("hello");
        for (let index = 0; index < 5; index++) {
            console.log(this.section_position[index]);
        }
    },
    methods: {
        anchorClick(i) {
            console.log(i)
            for (let index = 0; index < 5; index++) {
                if (index == i ) {
                    this.anchor_isActive[index] = true;
                    this.$set(this.anchor_isActive, index, true);
                }else {
                    this.anchor_isActive[index] = false;
                }
            }
        },
        getSectionY() {
            var y_coordinate = window.pageYOffset;
            // var section_position = [];
            // var window_top = window.top.scrollY;
            // console.log(window_top)
            // console.log(y_coordinate);
            // section_position[0] = this.$refs.first_section_ref.getBoundingClientRect().top;
            // section_position[1] = this.$refs.second_section_ref.getBoundingClientRect().top;
            // section_position[2] = this.$refs.third_section_ref.getBoundingClientRect().top;
            // section_position[3] = this.$refs.fourth_section_ref.getBoundingClientRect().top;
            // section_position[4] = this.$refs.last_section_ref.getBoundingClientRect().top;
            // console.log("hello");
            // for (let index = 0; index < 5; index++) {
            //     console.log(section_position[index]);
            // }
            if (y_coordinate > this.section_position[0] && y_coordinate < this.section_position[1]) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 0, true);
            }
            if (y_coordinate > this.section_position[1] && y_coordinate < this.section_position[2]) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 1, true);
            }
            if (y_coordinate > this.section_position[2] && y_coordinate < this.section_position[3]) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 2, true);
            }
            if (y_coordinate > this.section_position[3] && y_coordinate < this.section_position[4]) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 3, true);
            }
            if (y_coordinate > this.section_position[4]-100 && y_coordinate < this.section_position[4]+100) {
                for (let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 4, true);
            }
        }
    },
}
</script>
