<script>
export default {
    components: {

    },
    data() {
        return {
            background_height: [],
            background_width: [],
            anchor_isActive: [],
            section_position: [],
            section_position_mobile: [],
            section_position_tablet: [],
            overlay: false,
        }
    },
    created() {
        window.addEventListener('mousewheel', this.determineSectionY, {
            passive: false
        });
    },
    mounted:function() {
        
        this.section_position[0] = this.$refs.first_section_ref.getBoundingClientRect().top;
        this.section_position[1] = this.$refs.second_section_ref.getBoundingClientRect().top;
        this.section_position[2] = this.$refs.third_section_ref.getBoundingClientRect().top;
        this.section_position[3] = this.$refs.fourth_section_ref.getBoundingClientRect().top;
        this.section_position[4] = this.$refs.last_section_ref.getBoundingClientRect().top;
        
        this.section_position_mobile[0] = this.$refs.first_section_ref_mobile.getBoundingClientRect().top;
        this.section_position_mobile[1] = this.$refs.second_section_ref_mobile.getBoundingClientRect().top;
        this.section_position_mobile[2] = this.$refs.third_section_ref_mobile.getBoundingClientRect().top;
        this.section_position_mobile[3] = this.$refs.fourth_section_ref_mobile.getBoundingClientRect().top;
        this.section_position_mobile[4] = this.$refs.last_section_ref_mobile.getBoundingClientRect().top;

        this.section_position_tablet[0] = this.$refs.first_section_ref_tablet.getBoundingClientRect().top;
        this.section_position_tablet[1] = this.$refs.second_section_ref_tablet.getBoundingClientRect().top;
        this.section_position_tablet[2] = this.$refs.third_section_ref_tablet.getBoundingClientRect().top;
        this.section_position_tablet[3] = this.$refs.fourth_section_ref_tablet.getBoundingClientRect().top;
        this.section_position_tablet[4] = this.$refs.last_section_ref_tablet.getBoundingClientRect().top;
        for (let index = 0; index < 5; index++) {
            this.anchor_isActive[index] = false;
        }
        this.$set(this.anchor_isActive, 0, true);
        var window_width = window.innerHeight;
        // console.log(window_width);
        // this.$set(this.background_height, 0, window_width);
        // var window_height = window_width * 3;
        // this.$set(this.background_height, 0, window_height);
        // this.getSectionY();
        console.log("hello");
        for (let index = 0; index < 5; index++) {
            console.log(this.section_position[index]);
            console.log(this.section_position_tablet[index]);
            console.log(this.section_position_mobile[index]);
        }
    },
    methods: {
        anchorClick(i) {
            for (let index = 0; index < 5; index++) {
                if (index == i ) {
                    this.anchor_isActive[index] = true;
                    this.$set(this.anchor_isActive, index, true);
                    console.log("anchor is clicked",this.anchor_isActive[index])
                }else {
                    this.anchor_isActive[index] = false;
                }
            }
        },
        formatPosition(){
            this.$nextTick(()=>{
                this.$refs.first_section_ref.ScrollTop=0;
            });
        },
        determineSectionY() {
            var y_coordinate = window.pageYOffset;
            console.log(y_coordinate)
            if(y_coordinate == 0) {
                for(let index = 0; index < 5; index++) {
                    this.anchor_isActive[index] = false;
                }
                this.$set(this.anchor_isActive, 0, true);
            }else {
                if (y_coordinate > this.section_position[0] && y_coordinate < this.section_position[1]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 0, true);
                }
                if (y_coordinate > this.section_position[1] && y_coordinate < this.section_position[2]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 1, true);
                }
                if (y_coordinate > this.section_position[2] && y_coordinate < this.section_position[3]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 2, true);
                }
                if (y_coordinate > this.section_position[3] && y_coordinate < this.section_position[4]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 3, true);
                }
                if (y_coordinate > this.section_position[4]-50 && y_coordinate >500) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 4, true);
                }
                // In Mobile Layout
                if (y_coordinate > this.section_position_mobile[0] && y_coordinate < this.section_position_mobile[1]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 0, true);
                }
                if (y_coordinate > this.section_position_mobile[1] && y_coordinate < this.section_position_mobile[2]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 1, true);
                }
                if (y_coordinate > this.section_position_mobile[2] && y_coordinate < this.section_position_mobile[3]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 2, true);
                }
                if (y_coordinate > this.section_position_mobile[3] && y_coordinate < this.section_position_mobile[4]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 3, true);
                }
                if (y_coordinate > this.section_position_mobile[4]-100 && y_coordinate < this.section_position_mobile[4]+100) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 4, true);
                }

                // In Tablet Layout
                if (y_coordinate > this.section_position_tablet[0] && y_coordinate < this.section_position_tablet[1]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 0, true);
                }
                if (y_coordinate > this.section_position_tablet[1] && y_coordinate < this.section_position_tablet[2]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 1, true);
                }
                if (y_coordinate > this.section_position_tablet[2] && y_coordinate < this.section_position_tablet[3]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 2, true);
                }
                if (y_coordinate > this.section_position_tablet[3] && y_coordinate < this.section_position_tablet[4]) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 3, true);
                }
                if (y_coordinate > this.section_position_tablet[4]-100 && y_coordinate < this.section_position_tablet[4]+100) {
                    for (let index = 0; index < 5; index++) {
                        this.anchor_isActive[index] = false;
                    }
                    this.$set(this.anchor_isActive, 4, true);
                }
            }
        },
        async getSectionY() {
            this.section_position[0] = this.$refs.first_section_ref.getBoundingClientRect().top-200;
            this.section_position[1] = this.$refs.second_section_ref.getBoundingClientRect().top;
            this.section_position[2] = this.$refs.third_section_ref.getBoundingClientRect().top;
            this.section_position[3] = this.$refs.fourth_section_ref.getBoundingClientRect().top;
            this.section_position[4] = this.$refs.last_section_ref.getBoundingClientRect().top;
        }
    },
};
